@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
	margin: 0;
	font-family: 'Poppins', sans-serif;
	-webkit-font-smoothing: 'Poppins', sans-serif;
	-moz-osx-font-smoothing: 'Poppins', sans-serif;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.nav {
	height: 80px;
	background: white;
}
.account {
	background: #8d39fa;
	border: none;
}
.account:hover {
	background: #591da8;
}

.shop {
	background: transparent;
	border: #8d39fa 1px solid;
	color: #8d39fa;
}

.shop:hover {
	border: #8d39fa 1px solid;
	background: #8d39fa;
	color: white;
}

.shop,
.account {
	outline: none;
	border-radius: 42px;
	font-size: small;
	font-weight: 700;
}
.links,
.drop-down {
	color: #333;
	padding: 0 24px;
	display: inline-block;
	color: #333;
	font-weight: 500;
	font-size: 15px;
	text-transform: uppercase;
	background: #f8f9fa;
	border: none;
	outline: 0;
	box-shadow: none;
}
.drop-down {
	margin-top: 3px;
	background: #f8f9fa;
}
.drop-down:hover {
	/* background: #f8f9fa; */
	border: none;
	outline: 0;
	box-shadow: none;
}

.carousel-image {
	height: 84vh;
}

.panner {
	width: 33rem;
	display: flex;
	align-items: flex-end;
}

.cat-image {
	width: 360px;
	height: 360px;
	transition: all 0.3s ease-in;
	padding-bottom: 1rem;
}
.cats-row {
	display: flex;
	align-items: end;
	justify-content: space-between;
}

.cats-row .category {
	width: 360px;
}

.cat-image:hover {
	opacity: 0.88;
	-webkit-filter: 0 4px 4px 4px rgba(0, 0, 0, 25);
	        filter: 0 4px 4px 4px rgba(0, 0, 0, 25);
	-webkit-transform: scale3d(1.05, 1.05, 1);
	        transform: scale3d(1.05, 1.05, 1);
}

.image-container {
	background: #f9f9f9;
	height: 460px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.landing div:last-child {
	margin-top: 47px;
}

.panner-container {
	display: flex;
	justify-content: center;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10rem;
}

.form-container h1 {
    font-weight: bold;
    font-size: 48px;
    color: #5d54a4;
    margin-bottom: 2rem;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: start;
}

.content > input {
    margin-bottom: 1rem;
    padding: 0.5rem 1rem;
    border-radius: 6px;
    border: 1px solid #d4d4d4;
    width: 364px;
    font-size: 17px;
}

.content > input:focus {
    outline: none;
}

.content > label {
    color: #333333;
    font-size: 15px;
    margin-bottom: 0.4rem;
}

.signup-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.signup-button {
    background: #5d54a4;
    color: #fff;
    border: none;
    border-radius: 40px;
    font-size: 24px;
    padding: 0.5rem 2rem;
    margin-top: 1rem;
}

.signup-p {
    margin-top: 0.5rem;
    font-size: 18px;
    color: #959595;
}

.signup-p Link {
    color: #5d54a4;
}

.related-products {
	margin-top: 200px;
}
.details {
	display: flex;
	flex-direction: column;
	justify-items: space-around;
}

.parent-product {
	text-align: center;
	-webkit-perspective: 1000px;
	        perspective: 1000px;
}
.product-options {
	opacity: 0;
	position: absolute;
	right: -20px;
	top: 163px;
	transition: all 0.2s ease-in;
}

.parent-product:hover .product-options {
	opacity: 1;
	right: 0px;
}
.product-card {
	position: relative;
	background: #f9f9f9;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 380px;
	width: 260px;
	overflow: hidden;
}
.product-image {
	height: 300px;
	width: 100%;
	transition: all 0.2s ease-in;
}
.product-image:hover {
	-webkit-transform: scale3d(1.1, 1.1, 1);
	        transform: scale3d(1.1, 1.1, 1);
}

.product-options ul {
	list-style: none;
	font-size: 1.6rem;
}

.product-options ul li {
	background: #591da8;
	margin-top: 2px;
	padding: 5px 10px;
	color: #fff;
	transition: all 0.2s ease-in;
	cursor: pointer;
}
.product-options ul li:hover {
	background: #ee1e90;
}

#menu {
	position: fixed;
	margin-top: 20px;
	z-index: 1020 !important;
}

#collapse {
	background: #591da8;
	color: white;
	border: none;
	outline: none;
}

#collapse:hover {
	background: #ee1e90;
}
#options {
	background: #591da8;
}

